.home__blog{
    display: flex;
    flex-direction: column;

}

.home__blog-heading{
    width:100%;
    text-align: left;
    margin-bottom: 5rem;
}

.home__blog-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
    text-align: center;
}

.home__blog-container{
    display: flex;
    flex-direction: row;

}

.home__blog-container_groupA{
    flex: 0.75;
    margin-right: 2rem;

}
.home__blog-container_groupB{
    flex: 1;
    display:grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 2rem;
    
}